import React from 'react';

import '../assets/styles/components/OneChat.scss';
import perfil from '../assets/static/Mask-Perfil.png';
import doctora from '../assets/static/medica8.png';
import NavigatorNav from '../components/NavigatorNav';

import HeadBack from '../components/HeadBack';
import HeadChat from '../components/HeadChat';
import Chat1 from '../components/Chat1';
import Chat2 from '../components/Chat2';
import Chat1B from '../components/Chat1B';
import Answer from '../components/Answer';

const OneChat = () => (
  <>
    <div className='contenedor_screenChat'>
      <HeadBack link='/' nameHead='Mensajes' />
      <div className='contenedorOneChat'>
        <HeadChat nameHead='Dra. Rosa Rojas' photoProfileHead={perfil} />
        <Chat1 photoProfile1={perfil} name1='Juana Mara' lastMessage1='Buenas tardes doctora Rosa, me gustara tener una consulta con usted acerca de mi parto' />
        <Chat2 photoProfile2={doctora} name2='Dra. Rosa Rojas' lastMessage2='Hola Juana! Por supuesto, cmo podra apoyarte?' />
        <Chat1B photoProfile1B={perfil} name1B='Juana Mara' lastMessage1B='Mi obstetra me ha recomendado una cesrea, pero no entiendo por qu, no me gustara ir a una clnica en pandemia, y hace poco me enter por Mariana Rivas que es posible tener un parto en casa, y me dijo que hablara con ustd.' />
        <Chat2 photoProfile2={doctora} name2='Dra. Rosa Rojas' lastMessage2='As es, si deseas podemos coordinar una evaluacin y ver si es posible, te gustara?' />
        <Chat1 photoProfile1={perfil} name1='Juana Mara' lastMessage1='S por favor, podra este martes a las 4pm por videollamada?' />
        <Chat2 photoProfile2={doctora} name2='Dra. Rosa Rojas' lastMessage2='S! Listo :) Quedamos en contacto, un abrazo.' />
        <Answer />
      </div>
    </div>
    <NavigatorNav />
  </>
);

export default OneChat;
